
import { Injectable } from "@angular/core";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { Observable } from "rxjs/Observable";

import { VrtlUser } from '../_models/user.model';

@Injectable()
export class AuthToken {
    private _token: BehaviorSubject<string> = new BehaviorSubject<string>(null);
    private _user: BehaviorSubject<VrtlUser> = new BehaviorSubject<VrtlUser>(null);
    public tokenEmitter: Observable<string> = this._token.asObservable();
    public userEmitter: Observable<VrtlUser> = this._user.asObservable();
    
    constructor() {console.log('AuthToken');}

    setToken(token: string) {
      if (token != null || token != undefined) 
        this._token.next(token);
    }

    setUser(user: VrtlUser) {
      if (user != null || user != undefined) 
        this._user.next(user);
    }
}

