
import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Message } from 'primeng/primeng';

import { DataService } from '../_services/data.service';
import { URL_CONSTANTS } from '../_common/appconfig';
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
// import { Util } from '../_common/util';

declare var __LOCAL: string;

@Component({
  moduleId: module.id,
  selector: 'menu-dwellings',
  templateUrl: './dwellings.component.html'
})
export class estateDwellingsComponent implements OnInit {
  hdr: string;
  showDlg: boolean = false;
  editRec: boolean = false;
  msgs: Message[] = [];
  loading: boolean;
  dwellings: [];
  // loantype: string;

  constructor(private service: DataService, private route: ActivatedRoute, private confirm: ConfirmationService) { }

  ngOnInit() {
    this.hdr = 'All Dwellings';
    console.log('Dwellings called');
    this.dwellings = [];
    __LOCAL ? this.getDummyEstateDwellings(): this.getEstateDwellings();
  }

  getDummyEstateDwellings() {
    this.loading = true;
    let params = [];
    this.service.getDummyData("./assets/data/dwellings.json", this.callBackEstateDwellings.bind(this), {params});
    setTimeout(() => { this.loading = false; }, 30000);
  }

  getEstateDwellings() {
    this.loading = true;
    let params = [];
    console.log('get ...',URL_CONSTANTS.ESTATEDWELLINGS);
    this.service.getData(URL_CONSTANTS.ESTATEDWELLINGS, this.callBackEstateDwellings.bind(this), params);
    setTimeout(() => { this.loading = false; }, 30000);
  }

  callBackEstateDwellings(_data: any) {
    this.dwellings = _data;
    this.loading = false;
  }

  onRowDblClk(event) {
  }

  onDlgShow(event) {
    if (this.editRec) {

    }
  }

}
