import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
//import { Store } from '@ngrx/store';
import { Router } from '@angular/router';

import { Message } from 'primeng/primeng';
import { SelectItem } from 'primeng/primeng';

// import * as fromApp from './auth.reducers';
// import * as AuthActions from './auth.actions';
import { AuthService } from './auth.service';

import { VrtlUser } from '../_models/user.model';
import { URL_CONSTANTS, LANDING_URL } from '../_common/appconfig';

@Component({
  selector: 'app-signin',
  templateUrl: './login.component.html'
})
export class SigninComponent implements OnInit {
  msgs: Message[] = [];
  supportType: string;
  estateSupport: boolean;
  supportlist: SelectItem[];
  constructor(private authService: AuthService, private router: Router) {
    this.supportlist = [
      {label:'Customer Service', value:'cust'},
      {label:'Estate Management', value:'estate'}
    ];
  }

  ngOnInit() {
    this.supportType='estate';
    this.estateSupport=false;
  }

  onLogin(user: VrtlUser) {
    console.log ('Found valid customer data');
    this.estateSupport = user.estateSupport;
    let link = this.estateSupport ? 'DAILY':'COMMUNITY';
    switch (link){
      case 'COMMUNITY' : { this.router.navigate(['/community']); break;}
      case 'DAILY' : { this.router.navigate(['/daily']); break;}
      default: { this.router.navigate(['/daily']); break;}
    }
  }

  onLoginError() {
    this.msgs = [];
    this.msgs.push({ severity: 'info', summary: 'Info Message', detail: 'Incorrect customer ID and/or estate id' });
  }

  onSignin(form: NgForm) {
    this.msgs = [];
    const sType = this.supportType;
    if ( sType === 'estate'){
      const estateId = form.value.estateId;
      const customerId = 'estateuser';
      this.authService.signinestate(estateId, this.onLogin.bind(this), this.onLoginError.bind(this));
    } else{
      const customerId = form.value.customerId;
      const estateId = 'aquesa';
      //this.store.dispatch(new AuthActions.TrySignin({username: email, password: password}));
      this.authService.signinUser(customerId, estateId, this.onLogin.bind(this), this.onLoginError.bind(this));
    }
  }
}

