import { SelectItem } from "primeng/primeng";

declare function formatNumbers(any) : any;
declare function dates2Strings(any) : any;
declare function remove$Dates(any) : any;
declare function getValue(Object, string) : any;
declare function b64toBlob(string, any ) : any;
declare function doy2Date(string): Date;
declare function trimTimeInObj(any, string): any;
declare const NETWORKCODES: any;

export class Util {
  private static _today: Date = new Date();

  constructor() {
  }

  static getToday() {
    let dt = new Date();
    let tm = Util._today.getTime();
    dt.setTime(tm);
    return dt;
  }

  static setToday(today: Date) {
    // debugger;
    // console.log("Today is: ", today);
    // Util._today = new Date();
    if (today != null && today != undefined && !isNaN(today.getDate())) {
      Util._today = new Date(today);
      console.log("Today is: ", this._today);
    }
    else {
      Util._today = new Date();
      console.log("Default date is: ", this._today);
    }
  }

  static clone(rec: Object) {
    let str = JSON.stringify(rec);
    return JSON.parse(str);
  }

  static sleepFor(sleepDuration) {
    var now = new Date().getTime();
    while (new Date().getTime() < now + sleepDuration) { /* do nothing */ }
  }

  static getIdByCode(cd: string, arr: { code: string, id?: number }[]) {
    let _id: number = null;
    arr.forEach(ele => {
      if (ele.code === cd) {
        _id = ele.id;
      }
    });
    return _id;
  }

  static local2UTC(dt: number) {
    let day = new Date();
    let off = day.getTimezoneOffset() * 60000;
    let utc = dt + off;
    day.setTime(utc);
    return day;

    // return new Date(
    //     Date.UTC(
    //       dt.getFullYear(), dt.getMonth(), dt.getDay(),
    //       dt.getHours(), dt.getMinutes(), dt.getSeconds(), dt.getMilliseconds()
    //     ));
  }

  static utc2Local(dt: number) {
    let day = new Date();
    let off = day.getTimezoneOffset() * 60000;
    let local = dt - off;
    day.setTime(local);
    return day;
  }

  static FormatNumbers(jobj){
    return formatNumbers(jobj);
  }

  static Dates2Strings(jobj){
    dates2Strings(jobj);
    return remove$Dates(jobj);
  }

  static TrimTimeFromDates(jobj,key){
    return trimTimeInObj(jobj,key);
  }

  static GetValue(jobj, key){
    return getValue(jobj, key);
  }

  static Base64Decode(str, ty){
    return b64toBlob(str, ty);
  }

  static DOY2Date(str){
    return doy2Date(str);
  }

  static isValidDateStr(dateStr) {
    const regexExp = /^\d{4}-\d{2}-\d{2}$/;
    if (dateStr.match(regexExp) === null) { return false;}
    const date = new Date(dateStr);
    const timestamp = date.getTime();
    if (typeof timestamp !== 'number' || Number.isNaN(timestamp)) {return false;}
    return true;
  }

  static getNetworkRcText(_cd: string){
    let rc =[];
    rc = NETWORKCODES.hasOwnProperty(_cd) ?
      [NETWORKCODES[_cd].ISOReason, NETWORKCODES[_cd].ReasonforFailure, NETWORKCODES[_cd].ActionRequired]
      :
      ['Unknown NetworkRc code','','Please contact technical support team'];
    return rc;
  }
}