
import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SelectItem } from 'primeng/primeng';
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { UIChart } from "primeng/components/chart/chart";
import { DataService } from '../_services/data.service';

import { Util } from '../_common/util';

declare var __LOCAL: string;

@Component({
  moduleId: module.id,
  selector: 'menu-weekly',
  templateUrl: './weekly.component.html'
})

export class EstateWeeklyConsumption implements OnInit {
    @ViewChild('chart') chart: UIChart;
    basicData: any;
    hdr: string;
    basicOptions: any;
    chartTypes: SelectItem[] = [
      {label:'Bar', value: 'bar'},
      {label: 'Line', value: 'line'}, {label: 'Doughnut', value: 'doughnut'},
      {label: 'Pie', value: 'pie'}, {label:'Table', value: 'table'}];
    dwellingList: SelectItem[] =[
      {label:'All', value: 'all'}, {label:'A101', value: 'A101'},
      {label: 'B101', value: 'B101'}, {label: 'C101', value: 'C101'}];
    chartType: string;
    dwelling: string;
    loading: boolean;
    labels: string[];
    data: string[];
    datatable: boolean;
    weeklyData: any;

    constructor(private service: DataService, private route: ActivatedRoute, private confirm: ConfirmationService) { }

    ngOnInit() {
      this.hdr = 'Estate Weekly Water Consumption';

        console.log("Weekly Called");
        this.chartType = 'bar';
        this.dwelling = 'all';
        this.datatable = false;
        this.setOptions();

        this.basicData = {
            labels: [
              '00', '01', '02', '03', '04', '05', '06' ],
            datasets: [
                {
                    label: 'Weekly Water Consumption',
                    data: [540, 325, 702, 620, 540, 325, 702],
                    backgroundColor: ['rgba(255, 159, 64, 0.2)', 'rgba(75, 192, 192, 0.2)', 'rgba(54, 162, 235, 0.2)', 'rgba(153, 102, 255, 0.2)'],
                    // borderColor: ['rgb(255, 159, 64)', 'rgb(75, 192, 192)', 'rgb(54, 162, 235)', 'rgb(153, 102, 255)'],
                    // borderWidth: 1
                }
            ]
        };
        __LOCAL ? this.getDummyWeeklyData(): this.getWeeklyData();


    }

    setOptions(){
      const documentStyle = getComputedStyle(document.documentElement);
      const textColor = documentStyle.getPropertyValue('--text-color');
      const textColorSecondary = documentStyle.getPropertyValue('--text-color-secondary');
      const surfaceBorder = documentStyle.getPropertyValue('--surface-border');
      this.basicOptions = {
        plugins: {
            legend: {
                labels: {color: textColor}
            }
        },
        scales: {
            y: {
                beginAtZero: true,
                ticks: {color: textColorSecondary},
                grid: {color: surfaceBorder, drawBorder: false}
            },
            x: {
                ticks: {color: textColorSecondary},
                grid: {color: surfaceBorder, drawBorder: false}
            }
        }
      };
    }

    getDummyWeeklyData() {
      this.loading = true;
      let params = [];
      this.service.getDummyData("./assets/data/weekly.json", this.callBackWeeklyData.bind(this), {params});
      setTimeout(() => { this.loading = false; }, 30000);
    }

    getWeeklyData(){


    }

    callBackWeeklyData(_data: any){
      // console.log(_data);
      this.labels = [];
      this.data = [];
      this.weeklyData = _data;
      _data.forEach(ele => {
        // let d1 = ele;
        this.labels.push(Util.GetValue(ele, 'day'));
        this.data.push(Util.GetValue(ele, 'value'));
      });
      this.basicData.labels = this.labels;
      this.basicData.datasets[0].data = this.data;
      this.datatable = this.chartType == 'table' ? true : false;
      if (!this.datatable) {this.chart.reinit()};
      this.loading = false;
    }



    changeChart(event: any) {
      console.log("ChartType Changed", event);
      this.basicData.type = this.chartType; //doughnut, Pie, Line
      this.datatable = this.chartType == 'table' ? true : false;
      setTimeout(() => {
         if (!this.datatable) {this.chart.reinit()};
      }, 100);
    }

    changeDwelling(event: any){
      console.log("Dwelling Changed", event);
    }
}

