
import { Component, OnInit, ViewChild } from '@angular/core';

import { Message } from 'primeng/primeng';
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';

import { DataService } from '../_services/data.service';
import { iNotification, Notification } from '../_models/notification.model';
import { URL_CONSTANTS } from '../_common/appconfig';
import { Util } from '../_common/util';

declare var __LOCAL: string;
declare var __CUSTOMERID:string;

@Component({
  moduleId: module.id,
  selector: 'menu-notification',
  templateUrl: './notification.component.html'
})
export class NotificationComponent implements OnInit {
  notifications: Notification[] = [];
  errorMessage: string;
  hdr: string;
  showDlg: boolean = false;
  editRec: boolean = false;
  rec: Notification;
  msgs: Message[] = [];
  loading: boolean;
  customerId: string;

  constructor(private service: DataService, private confirm: ConfirmationService) { }

  ngOnInit() {
    this.customerId = __CUSTOMERID;
    __LOCAL ? this.getDummyNotifications(): this.getNotifications();
  }

  getDummyNotifications() {
    this.loading = true;
    this.service.getDummyData("./assets/data/notifications.json", this.callBack.bind(this));
    setTimeout(() => { this.loading = false; }, 30000);
  }

  getNotifications() {
    this.loading = true;
    console.log('get ...',URL_CONSTANTS.NOTIFICATION);
    this.service.getData(URL_CONSTANTS.NOTIFICATION, this.callBack.bind(this));
    setTimeout(() => { this.loading = false; }, 30000);
  }

  callBack(arr: Notification[]) {
    let d1 = {payments: arr};
    Util.Dates2Strings(<unknown>d1);
    Util.FormatNumbers(<unknown>d1);
    this.notifications = arr;
    this.loading = false;
  }

  onRowDblClk(event) {
    this.showDlg = true;
    this.editRec = true;
    this.rec = <Notification>Util.clone(event.data); //event.data is selected row data
    this.hdr = 'Inquire Notification';
  }

  onDlgShow(event) {
    if (this.editRec) {

    }
  }
}
