  ngOnInit() {
    // console.log(this.user);
    this.showEditDlg = false;
    this.dlgWidth = window.innerWidth > 780 ? 780 : window.innerWidth;
    console.log('Window Width:  ', this.dlgWidth);
    console.log("COmmunities Called");
    __LOCAL ? this.getDummyCommunitiesData(): this.getCommunitiesData();
    this.ctryList = ['India', 'Pakistan', 'Bangladesh'];
    this.dwtypes = ['1BHK', '2BHK', '2.5BHK', '3BHK', '3.5BHK', '4BHK', 'Villa', 'Villment'];
    this.tags = [];
  }

  filterCountry(event: AutoCompleteCompleteEvent) {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered: any[] = [];
    let query = event.query;

    for (let i = 0; i < (this.ctryList as any[]).length; i++) {
        let country = (this.ctryList as any[])[i];
        if (country.toLowerCase().indexOf(query.toLowerCase()) == 0) {
            filtered.push(country);
        }
    }
    this.filteredCountries = filtered;
    // console.log("filter county list", this.filteredCountries);
  }

  filterdwtypes(event: AutoCompleteCompleteEvent) {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered: any[] = [];
    let query = event.query;

    for (let i = 0; i < (this.dwtypes as any[]).length; i++) {
        let dw = (this.dwtypes as any[])[i];
        if (dw.toLowerCase().indexOf(query.toLowerCase()) == 0) {
            filtered.push(dw);
        }
    }
    this.fldwtypes = filtered;
    // console.log("filter dw types", this.fldwtypes);
  }

  filtertags(event: AutoCompleteCompleteEvent) {
    //in a real application, make a request to a remote url with the query and return filtered results, for demo we filter at client side
    let filtered: any[] = [];
    let query = event.query;

    for (let i = 0; i < (this.tags as any[]).length; i++) {
        let dw = (this.tags as any[])[i];
        if (dw.toLowerCase().indexOf(query.toLowerCase()) == 0) {
            filtered.push(dw);
        }
    }
    if (!filtered.length) {filtered.push(query);}
    this.fltags = filtered;
    // console.log("filter Tags list", this.fltags, query);
  }

  getDummyCommunitiesData(){
    this.loading = true;
    let params = [];
    this.service.getDummyData("./assets/data/communities.json", this.callBackCommunitiesData.bind(this), {params});
    setTimeout(() => { this.loading = false; }, 30000);
  }

