  onComtySelect(event) {
    // console.log(event);
    this.selectRec = true;
  }

  onComtyUnSelect(event) {
    // console.log(event);
    this.selectRec = false;
  }

  onEdit(event) {
    this.selectRec = true;
    this.rec = <Community>Util.clone(event);
    this.hdr = this.isEditable(this.rec) ? 'Update Community' : 'View Community';
    // this.setFormData();
    this.currRec = this.copyForUpdate(this.rec);
    this.showEditDlg = true;
  }

  onAdd() {
    this.rec = new Community('');
    this.showEditDlg = true;
    this.selectRec = false;
    this.hdr = 'Add Community';
  }

  copyForUpdate(from: Community) {
    let to = <Community>Util.clone(from);
    return to;
  }

