import { Component, OnInit } from '@angular/core';
import { MenubarModule, MenuItem } from 'primeng/primeng';
import { Router } from '@angular/router';

import { DropDownDirective } from '../_directives/dropdown.directive';
import { AuthService } from '../auth/auth.service';
import { AuthToken } from '../auth/authtoken';
import { VrtlUser } from '../_models/user.model';


//declare var showMenu: boolean;

@Component({
  moduleId: module.id,
  selector: 'vrtl-menu',
  templateUrl: './vrtl-menu.component.html'
})
export class VrtlMenuComponent implements OnInit {
  private items: MenuItem[];
  displayMenu: boolean;
  user: VrtlUser;
  userName: string;
  estateSupport: boolean;

  constructor(public auth: AuthService, private router: Router, private aToken: AuthToken) {
    this.aToken.userEmitter.subscribe((_token) => {
      console.log("atoken.userEmitter :", _token);
      this.user = _token;
      if (this.user != null && this.user != undefined){
        // this.userName = this.user.firstName + ' ' + this.user.lastName;
        this.userName = this.user.customerId + ' ' + this.user.estateId;
        this.estateSupport = this.user.estateSupport;
      }
    });
  }

  ngOnInit() {
    this.userName = '';
    this.estateSupport = true;
  }

  onLogout() {
    this.auth.logout();
    this.user = null;
    this.userName = '';
    this.router.navigate(['/']);
  }
}