import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
//import { saveAs } from 'file-saver/FileSaver';

import { URL_CONSTANTS } from '../_common/appconfig';

@Injectable()
export class DataService {
  constructor(private http: HttpClient) { }

  deleteData(datatype: string, key: string, callback: Function = null) {
    let url = URL_CONSTANTS.BASE_URL + datatype + '/' + key;
    this.http
      .delete(url)
      .subscribe(
      data => {
        // console.log(data);
        if (callback != null) { callback(data); }
      });
  }

  updateData(datatype: string, key: string, obj: any, callback: Function = null) {
    let url = URL_CONSTANTS.BASE_URL + datatype + '/' + key;
    this.http
      .put(url, obj)
      .subscribe(
      data => {
        // console.log(data);
        if (callback != null) { callback(data); }
      });
  }

  addData(datatype: string, obj: any, callback: Function = null) {
    // console.log(obj);
    let url = URL_CONSTANTS.BASE_URL + datatype;
    this.http
      .post(url, obj)
      .subscribe(
      data => {
        // console.log(data);
        if (callback != null) { callback(data); }
      });
  }

  constructUrl(_url: string, _params: { key: string, value: string }[]=null){
    if (_params != null && _params != undefined) {
      let p = '?';
      for (let i in _params) {
        p += i ? '&' : '';
        p += _params[i].key + '=' + _params[i].value;
      }
      _url += p;
    }
    return _url;
  }

  getData(datatype: string, callback: Function = null, params: { key: string, value: string }[] = null) {
    let url = URL_CONSTANTS.BASE_URL + datatype;
    if (params != null && params != undefined) {
      let p = '?';
      for (let i in params) {
        p += i ? '&' : '';
        p += params[i].key + '=' + params[i].value;
      }
      url += p;
    }
    console.log("GET :", url);
    this.http
      .get(url)
      .subscribe(
      data => {
        // console.log(data);
        if (callback != null) { callback(data); }
      });
  }

  getDataText(datatype: string, callback: Function = null, params: { key: string, value: string }[] = null) {
    let url = this.constructUrl(URL_CONSTANTS.BASE_URL + datatype, params);
    console.log("GET :", url);
    this.http
      .get(url, {responseType: 'text'})
      .subscribe(
      data => {
        if (callback != null) { callback(data); }
      });
  }

  // getFile(_url: string, fileType: string = 'pdf', fileName: string = undefined) {
  //   let url = URL_CONSTANTS.BASE_URL + _url;
  //   this.http
  //     .get(url, { observe: 'response', responseType: 'blob' })
  //     .subscribe(
  //     data => {
  //       let blob = new Blob([data.body], { type: 'application/pdf' });
  //       let fileUrl = window.URL.createObjectURL(blob);
  //       // if (fileName) saveAs(blob, fileName + fileType);
  //       if (fileName) saveAs(blob, fileName);
  //       else {
  //         if (window.navigator.msSaveOrOpenBlob) {
  //           window.navigator.msSaveOrOpenBlob(blob, fileUrl.split(':')[1] + '.pdf');
  //         } else {
  //           window.open(fileUrl);
  //         }
  //       }
  //     });
  // }

  getDummyData(url: string, callback: Function = null, options: any = {responseType: 'json'}) {
    console.log("GET :", url);
    this.http.get(url, options)
      .subscribe(
      data => {
        if (callback != null) { callback(data); }
      });
  }
}
