  // loantype: string;

  constructor(private service: DataService, private route: ActivatedRoute, private confirm: ConfirmationService) { }

  ngOnInit() {
    this.hdr = 'All Estate Users';
    console.log('Users called');
    this.users = [];
    __LOCAL ? this.getDummyEstateUsers(): this.getEstateUsers();
   }

  getDummyEstateUsers() {
    this.loading = true;
    let params = [];
    this.service.getDummyData("./assets/data/users.json", this.callBackEstateUsers.bind(this), {params});
    setTimeout(() => { this.loading = false; }, 30000);
  }

  getEstateUsers() {
    this.loading = true;
    let params = [];
    console.log('get ...',URL_CONSTANTS.ESTATEUSER);
    this.service.getData(URL_CONSTANTS.ESTATEUSER, this.callBackEstateUsers.bind(this), params);
    setTimeout(() => { this.loading = false; }, 30000);
  }

  callBackEstateUsers(_data: any) {
    this.users = _data;
    this.loading = false;
  }

