
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';

import { VrtlUser } from '../_models/user.model';
import { AuthToken } from './authtoken';
import { URL_CONSTANTS } from '../_common/appconfig';
import { Util } from '../_common/util';

declare var __LOCAL: string;
declare var __estateID:string;
declare var __CUSTOMERID:string;

@Injectable()
export class AuthService {
  token: string;
  user: VrtlUser;
  estateId: string;
  customerId: string;

  constructor(private aToken: AuthToken, private http: HttpClient) {
    this.init();
  }

  init(){
    this.user = null;
    this.token = null;
    this.estateId = null;
    this.customerId = null;
  }

  signinestate(estateId: string, callSuccess: Function, callFail: Function){
    this.user = null;
    this.estateId = estateId;
    this.customerId = 'estate Name';
    __LOCAL ?  this.verifyDummyestate(callSuccess, callFail): this.verifyestate(callSuccess, callFail);
  }

  verifyDummyestate(callSuccess: Function, callFail: Function){
    this.user = new VrtlUser();
    // this.user.firstName = this.customerId;
    // this.user.lastName = this.estateId;
    this.user.estateSupport = true;
    this.user.estateId = this.estateId;
    this.user.customerId = 'Ahaa Ohoo Community';
    this.aToken.setUser(this.user);
    this.setToken(this.customerId+':'+this.estateId);
    console.log("Set Token Dummy estate: ", this.customerId+':'+this.estateId);
    __CUSTOMERID = this.user.customerId;
    __estateID = this.user.estateId;
    callSuccess(this.user);
  }

  verifyestate(callSuccess: Function, callFail: Function){
    let url = URL_CONSTANTS.BASE_URL + URL_CONSTANTS.ESTATE + '?estateId=' + this.estateId;
    console.log(url);
    console.log(this);
    this.http
      .get(url, { observe: 'response' })
      .subscribe(
      resp => {
        console.log("estate Sign in", resp.body);
        if ( resp.body == null ){
          console.log('No estate Data');
          this.logout();
          callFail();
        }
        else{
          this.user = <VrtlUser>resp.body;
          this.user.estateSupport = true;
          console.log(this.user);
          this.aToken.setUser(this.user);
          this.customerId = this.user.customerId;
          this.estateId = this.user.estateId;
          this.setToken(this.customerId+':'+this.estateId);
          __CUSTOMERID = this.user.customerId;
          __estateID = this.user.estateId;
          callSuccess(this.user);
        }
      },
      (err: HttpErrorResponse) => {
        console.log('fetch estate details failed');
        console.log('Error Details', err);
        this.logout();
        callFail();
      }
      );
  }

  signinUser(customerId: string, estateId: string, callSuccess: Function, callFail: Function) {
    this.user = null;
    this.estateId = estateId;
    this.customerId = customerId;
    // this.setToken(this.customerId+':'+this.estateId);
    __LOCAL ?  this.verifyDummyCustomer(callSuccess, callFail): this.verifyCustomer(callSuccess, callFail);
  }

  logout() {
    // this.aToken.setToken(' '); // do not set to null for AuthToken
    this.aToken.setToken(''); // do not set to null for AuthToken
    this.aToken.setUser(null);
    this.init();
  }

  getToken() {
    return this.token;
  }

  setToken(tok: string) {
    this.token = tok;
    this.aToken.setToken(tok);
  }

  isAuthenticated() {
    return this.token != null;
  }

  verifyDummyCustomer(callSuccess: Function, callFail: Function) {
    // this.setToken('Basic bmlqZXNoOnBkcWF0MjAxNQ==');
    // this.isAdmin = true;
    this.user = new VrtlUser();
    // this.user.firstName = this.customerId;
    // this.user.lastName = this.estateId;
    this.user.estateId = this.estateId;
    this.user.customerId = this.customerId;
    this.user.estateSupport = false;
    this.aToken.setUser(this.user);
    this.setToken(this.customerId+':'+this.estateId);
    __CUSTOMERID = this.user.customerId;
    __estateID = this.user.estateId;
    console.log("Set Token Dummy Customer: ", this.customerId+':'+this.estateId);
    callSuccess(this.user);
  }

  // getUserRole(){
  //   return this.user != null ? this.user.role: null;
  // }

  verifyCustomer(callSuccess: Function, callFail: Function) {
    let url = URL_CONSTANTS.BASE_URL + URL_CONSTANTS.CUSTOMER + '?customerId=' + this.customerId;
    console.log(url);
    console.log(this);
    this.http
      .get(url, { observe: 'response' })
      .subscribe(
      resp => {
        console.log("Customer Sign in", resp.body);
        if ( resp.body == null ){
          console.log('No customer data found');
          this.logout();
          callFail();
        }
        else{
          this.user = <VrtlUser>resp.body;
          this.user.estateSupport = false;
          console.log(this.user);
          this.aToken.setUser(this.user);
          this.customerId = this.user.customerId;
          this.estateId = this.user.estateId;
          this.setToken(this.customerId+':'+this.estateId);
          __CUSTOMERID = this.user.customerId;
          __estateID = this.user.estateId;
          callSuccess(this.user);
        }
      },
      (err: HttpErrorResponse) => {
        console.log('fetch customer details failed');
        console.log('Error Details', err);
        this.logout();
        callFail();
      }
      );
  }
}
